}

@media screen and (min-width: 768px) {
  .p-news__button {
    margin-top: initial;
    margin-left: auto;
    background-color: #fff;
    color: #333;
    font-size: 1rem;
    font-weight: 500;
    padding: 0.3125rem 1.6875rem;
    position: relative;
    z-index: 1;
    overflow: hidden;
  }
}

@media screen and (min-width: 768px) {
  .p-news__button:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    background-color: #333;
    transform: translateX(-100%);
    transition: 0.3s ease-in-out;
  }
}

@media screen and (min-width: 768px) {
  .p-news__button:hover {
    color: #fff;
    transition: 0.3s ease-in-out;
  }
}

@media screen and (min-width: 768px) {
  .p-news__button:hover:before {
    transform: translateX(0);
  }
